<template>
  <div v-loading="loading" class="render-photo" :class="{ 'qt-position': $route.path.startsWith('/qt') }">
    <!-- 标题 -->
    <div v-if="!$route.path.startsWith('/qt')" class="render-photo-heading">
      <p>
        {{ $t("model.design_album") }}
        <i
          class="iconfont iconshuaxin"
          style="padding-left: 10px; font-size: 16px; color: #515151"
          @click="$act(getPhotos)"
        ></i>
      </p>
      <i class="iconfont icon-delete" @click="closeAlbum" />
    </div>
    <div v-if="photos.length > 0" class="scrollable-y render-photo-content">
      <!-- 各版本相册列表 -->
      <div v-for="version in serializePhotos" :key="version.vCode">
        <h3 class="version-title">{{ $t("model.photo_version") }} {{ version.vCode }}</h3>
        <ul style="padding: 24px 0">
          <template v-for="user in version.users">
            <li :key="user.userId" class="version-user">
              <!-- 用户信息 -->

              <div class="flex-space-between">
                <div class="user-info flex-vertical-center">
                  <BaseUser :user="user.userInfo" size="mini" />
                  <p class="upload-time">
                    {{ user.pictureCatalogList[0].gmtCreateTime | date("YYYY/MM/DD HH:mm") }}
                  </p>
                </div>
                <!--
                  <p>{{ $t('model.photo_version') }} {{ photo.vCode }}</p>
                -->
              </div>

              <!-- 图片列表 -->
              <ul class="photo-list">
                <li class="photo-item" v-for="picture in user.pictureCatalogList" :key="picture.pictureCatalogId">
                  <RenderPhotoItem
                    :from="from"
                    :picture="picture"
                    style="width: 140px; height: 140px"
                    @feedback="handleFeedback"
                    @delete="handleDeletePhoto"
                    @previewphotos="goToRenderPhotoPreview(picture)"
                    @download="downloadPhoto"
                    @magnify="
                      handleMagnifyPhoto(
                        user.pictureCatalogList.filter((picture) => picture.status === 2),
                        picture.thumbnail
                      )
                    "
                  />
                </li>
              </ul>
            </li>
          </template>
        </ul>
      </div>
    </div>

    <figure v-else-if="!loading" class="placeholder-empty flex-center">
      <img
        :src="require('@/assets/img/public/placeholder/placeholder-empty-photo.png')"
        width="109"
        alt="placeholder image for absence photos"
      />
      <figcaption class="empty-tip">
        <p v-html="$t('model.empty_rendered_images_tip')"></p>
      </figcaption>
    </figure>
  </div>
</template>

<script>
import BaseUser from '@/components/base/User';

import axios from '@/api/axios';
import { getFileRelateIdByCatalogId } from '@/loaders/catalog';
import { getModelInfo } from '@/api/shoe';
import { getHost } from '@/utils/exec';
import downloadByUrl from '@/utils/download-by-url';

import { getUsersInfoByIds } from '@/api/account';
import { openSingleton } from '@/utils/open-window';
import { updateWorkspaceRolesAndPermissions } from '@/loaders/context';
import { envs } from '@/utils/envs';
import delay from '@4dst-saas/public-utils/dist/delay';
import RenderPhotoItem from './components/RenderPhotoItem';
import RenderFeedback from './RenderFeedback.vue';
import { shoeViews, handbagViews } from './viewArr';

const getHandbagInfo = (handbagId) => {
  return axios.get('/bff-center/showCommon/handbagInfoSearch/getHandbagInfoAndTags', {
    params: {
      handbagId,
    },
    headers: {
      language: 'cn',
    },
  });
};

async function downloadByPost(url, data, fileName) {
  const form = document.createElement('form');
  form.action = `${url}?fileName=${fileName}`;
  form.target = '_blank';
  form.method = 'POST';
  Object.entries(data).forEach(([k, datum]) => {
    const input = document.createElement('input');
    input.name = k;
    input.value = datum;
    form.appendChild(input);
  });
  // console.log(form);
  form.style.position = 'fixed';
  form.style.top = '-9999px';
  form.style.bottom = '-9999px';
  document.body.appendChild(form);
  form.submit();
  delay(10000).then(() => {
    document.body.removeChild(form);
  });
}

// 获取相册列表
const apiGetModelRenderingPhotos = (catalogId, groupAppId, relateType = 0) => {
  const modelTypeMap = {
    0: 'SHOE',
    // 3: 'HANDBAG',
    3: 'SHOE',
    4: 'CLOTHES',
  };
  return axios({
    method: 'get',
    url: '/bff-center/cloud-disk/list/getCatalogPictureAlbumList',
    headers: { groupAppId },
    params: {
      catalogId,
      pictureAlbumType: modelTypeMap[relateType],
    },
  });
};
// 删除相册中的图片
const apiDeletePictureInPhoto = (id) => {
  return axios({
    method: 'post',
    url: '/bff-center/cloud-disk/operation/deleteCatalogPictureAlbum',
    params: { pictureCatalogId: id },
  });
};

export default {
  name: 'RenderPhoto',
  components: {
    RenderPhotoItem,
    BaseUser,
  },
  props: {
    from: String,
  },
  async created() {
    this.getPhotos();
    this.timer = setInterval(() => {
      this.getPhotos();
    }, 20000);

    this.host = await getHost();
    updateWorkspaceRolesAndPermissions(this.from);

    const relateType = this.$route.query.relateType || 0;
    const modelId = await getFileRelateIdByCatalogId({
      catalogId: this.$route.query.catalogId,
      workspaceId: this.from,
      relateType,
    });
    this.model = relateType.toString() === '0' ? (await getModelInfo(modelId)).data : (await getHandbagInfo(modelId)).data;
  },
  data() {
    return {
      model: null,
      photos: [],
      usersInfo: [],
      timer: null,
      host: null,
      loading: true,
    };
  },
  computed: {
    /** @return { Array<Object> } */
    serializePhotos() {
      if (this.photos.length === 0) {
        return [];
      }
      const vCodes = [...new Set(this.photos.map((photo) => photo.vCode))].sort(
        (a, b) => b - a,
      );
      return vCodes.map((ver) => {
        // 根据版本集合
        const versionPohots = this.photos.filter(
          (photo) => photo.vCode === ver,
        );
        const versionUserIds = [
          ...new Set(versionPohots.map((photo) => photo.userId)),
        ];
        const versionUsers = versionUserIds.map((userId) => {
          // 设置版本下的用户信息、渲染的图片
          const user = {};
          const pictureCatalogList = versionPohots.filter(
            (photo) => photo.userId === userId,
          );
          user.pictureCatalogList = pictureCatalogList;
          user.userId = pictureCatalogList[0].userId;
          user.userInfo = this.usersInfo.find((uInfo) => uInfo.id === userId);
          return user;
        });
        return {
          vCode: ver,
          users: versionUsers,
        };
      });
    },
  },
  methods: {
    async closeAlbum() {
      this.$closest('model').photoStatus = false;
    },
    // 获取相册渲染参数
    async getPhotos() {
      this.loading = true;
      try {
        const photos = (
          await apiGetModelRenderingPhotos(
            this.$route.query.catalogId,
            this.from,
            this.$route.query.relateType,
          )
        ).data;

        if (photos.length === 0) return;
        // 获取用户信息
        const userIds = [...new Set(photos.map((photo) => photo.userId))];

        this.usersInfo = (await getUsersInfoByIds(userIds)).data;

        // 如果渲染列表没有渲染中状态，停止定时器
        const renderingItems = photos.filter((item) => item.status <= 1);
        if (!renderingItems.length) {
          clearInterval(this.timer);
          this.timer = null;
        } else if (!this.timer) {
          this.timer = setInterval(() => {
            this.getPhotos();
          }, 20000);
        }

        this.photos = photos;
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.loading = false;
      }
    },
    // 处理放大镜效果
    async handleMagnifyPhoto(arr, imgUrl) {
      // 获取图片中的host, token, index等参数用于放大镜
      const imgUrls = arr
        .filter((item) => item.thumbnail)
        .map((pic) => pic.thumbnail);
      const index = imgUrls.findIndex((item) => item === imgUrl);
      const viewers = {
        srcList: imgUrls,
        index,
      };

      // 通知QT打开新的窗口显示放大镜
      if (!this.host) {
        this.$ImageViewer.show({
          ...viewers,
          targetElems: '.photo-item',
        });
      } else {
        localStorage.setItem(
          'imageViewer',
          JSON.stringify({
            ...viewers,
            list: viewers.srcList,
          }),
        );
        const { host } = window.location;
        this.host.openWin({
          url: `//${host}/qt/view-image`,
          params: viewers,
        });
      }
    },
    // 跳转到360图片预览
    goToRenderPhotoPreview(picture) {
      let path = picture.loadPath;
      if (path.indexOf('?') !== -1) {
        [path] = path.split('?');
      }
      if (this.host) {
        const { protocol, host } = window.location;
        console.info(this.from);
        this.host.openWin({
          url: `${protocol}//${host}/qt/render-photo-preview?p=${btoa(
            JSON.stringify({
              renderRelateId: picture.renderRelateId,
              albumId: picture.renderAlbumId,
              groupAppId: this.from,
            }),
          )}`,
        });
      } else {
        openSingleton(
          {
            name: 'RenderPhotoPreview',
            query: {
              // renderRelateId: picture.renderRelateId,
              // albumId: picture.renderAlbumId,
              // groupAppId: this.from,
              p: btoa(
                JSON.stringify({
                  renderRelateId: picture.renderRelateId,
                  albumId: picture.renderAlbumId,
                  groupAppId: this.from,
                }),
              ),
            },
          },
          true,
          'RenderPhotoPreview',
        );
      }
    },

    // 反馈云渲染效果
    async handleFeedback(picture) {
      // 通知QT打开新的窗口显示反馈弹窗
      if (!this.host) {
        try {
          const rt = await this.$injectDialog({
            is: RenderFeedback,
            props: {
              thumbnail: picture.thumbnail,
            },
          });
        } catch (err) {
          this.$errHandler(err);
        }
      } else {
        const { protocol, host } = window.location;
        this.host.openWin({
          url: `${protocol}//${host}/qt/offline/render-feedback?thumbnail=${encodeURIComponent(
            picture.thumbnail,
          )}`,
        });
      }
    },
    // 删除相册中的图片
    async handleDeletePhoto(id) {
      try {
        await apiDeletePictureInPhoto(id);
        this._deletePictureInPhotos(id);
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 在相册对象中删除对应id的图片
    _deletePictureInPhotos(id) {
      // 将用户信息放入对应的userId中
      this.photos.forEach((picture, i) => {
        if (picture.pictureCatalogId === id) {
          this.photos.splice(i, 1);
        }
      });
    },
    // 下载图片
    async downloadPhoto(picture) {
      const modelName = this.model.modelNumber;
      if (picture.imgType === 0) {
        const viewName = (this.$route.query.relateType.toString() === '0'
          ? shoeViews.find(v => v.value === picture.viewName)?.name : handbagViews.find(v => v.value === picture.viewName)?.name) || '';
        console.info(viewName);
        const fileName = `${modelName}${viewName ? `-${viewName}` : ''}.${picture.extData.extension}`;
        if (this.host) {
          this.host.downloadStaticFile(picture.downloadPath, fileName);
        } else {
          downloadByUrl(picture.downloadPath, fileName);
        }
      } else if (picture.imgType === 1) {
        const { renderRelateId, renderAlbumId } = picture;
        const items = (await axios.get(
          'bff-center/cloud-disk/list/getPanoramaPictureList',
          { params: { renderRelateId, albumId: renderAlbumId }, headers: { groupAppId: this.from } },
        )).data;
        const fileList = JSON.stringify(items);
        const url = `//${envs.VUE_APP_API_DOMAIN}/datapipe/repository/pack`;
        if (this.host) {
          this.host.downloadStaticFile(
            `${window.location.protocol}${url}?fileName=${modelName}`,
            `${modelName}.zip`,
            {
              method: 'POST',
              headers: {
                'Content-Type': 'application/x-www-form-urlencoded',
                // 'Content-Type': 'application/json',
              },
              body: `fileList=${encodeURIComponent(fileList)}`,
              // body: {
              // fileList: items,
              // },
            },
          );
        } else {
          downloadByPost(
            url,
            { fileList },
            modelName,
          );
        }
      }
    },
    // _downloadIamge(imgsrc) {
    //   const image = new Image();
    //   image.setAttribute('crossOrigin', 'anonymous');
    //   image.onload = () => {
    //     const extTmp = imgsrc.split('.').pop();
    //     const ext = ['png', 'jpg', 'jpeg'].includes(extTmp) || 'png';
    //     const fileName = imgsrc.split('/').pop() || 'unknown-file-name.png';

    //     const canvas = document.createElement('canvas');
    //     canvas.width = image.width;
    //     canvas.height = image.height;
    //     const context = canvas.getContext('2d');
    //     context.drawImage(image, 0, 0, image.width, image.height);
    //     const url = canvas.toDataURL(`image/${ext}`);
    //     const a = document.createElement('a');
    //     const event = new MouseEvent('click');
    //     a.download = fileName;
    //     a.href = url;
    //     a.dispatchEvent(event);

    //   };
    //   image.src = imgsrc;
    // },
  },
  destroyed() {
    clearInterval(this.timer);
  },
};
</script>

