<template>
  <div class="render-photo-item fullfilled">
    <!-- 四种状态：waiting, rendering, succeeded, failed -->
    <!-- 提交中 -->
    <!-- <div v-if="picture.status === 'waiting'"
     class="render-waiting flex-center fullfilled">
      <p>提交中...</p>
    </div> -->

    <!-- 渲染中 -->
    <div
      v-if="picture.status === 0"
      class="fullfilled render-rendering"
      v-loading="true"
      style="border-radius: 4px; overflow: hidden;"
      :element-loading-text="$t('public.waiting')"
      element-loading-background="#b5b2b5"
    >
      <span class="render-sequence">{{ $t("model.render_in_sequence") }}{{ picture.sequence }}</span>
    </div>
    <!-- 渲染中 -->
    <div
      v-if="picture.status === 1 && picture.imgType === 0"
      class="fullfilled render-rendering"
      v-loading="true"
      style="border-radius: 4px; overflow: hidden;"
      :element-loading-text="$t('public.rendering') + '...'"
      element-loading-background="#b5b2b5"
    ></div>

    <!-- 360°图渲染中 -->
    <div
      v-if="picture.status === 1 && picture.imgType === 1"
      class="fullfilled render-rendering"
      v-loading="true"
      style="border-radius: 4px; overflow: hidden;"
      :element-loading-text="`${$t('public.rendering')} ${picture.completedAmount}/${picture.totalAmount}...`"
      element-loading-background="#b5b2b5"
    >
      <span class="render-sequence">{{ $t("model.render_in_sequence") }}{{ picture.sequence }}</span>
    </div>

    <!-- 渲染失败 -->
    <div v-else-if="picture.status === 3" class="render-failed flex-center fullfilled">
      <span
        ><i class="iconfont iconfangong-loukong" />
        {{ $t("model.render_failed") }}
      </span>
      <span class="button-rerender" @click="renderRetry">重新渲染</span>
    </div>

    <!-- 渲染成功 显示图片 -->
    <div v-else-if="picture.status === 2" class="render-succeeded fullfilled">
      <base-image :src="picture.thumbnail" fit="contain" />
      <span class="picture-label flex-center">{{ resolution }}</span>
      <div class="modal"></div>
      <i
        class="iconfont el-icon-copy-document"
        v-if="$can('workspace', 'OP0161') && picture.imgType === 1"
        :data-clipboard-id="picture.pictureCatalogId"
        :data-clipboard-text="previewLink"
      >
      </i>
      <i class="el-icon-chat-line-square" @click="$emit('feedback', picture)" />
      <i v-if="isCreator" class="iconfont iconshanchu" @click="$emit('delete', picture.pictureCatalogId)" />
      <i class="iconfont icon-zoom" v-if="picture.imgType === 0" @click="$emit('magnify')" />
      <i class="iconfont icon-zoom" v-else @click="$emit('previewphotos')" />
      <i class="iconfont iconxiazaixuanrantu" v-if="$can(from, 'OP0160')" @click="$emit('download', picture)" />
      <div class="copy-tip-modal">
        <span class="copy-tip copy-success">{{ $t("copy_success") }}</span>
      </div>
      <div class="copy-tip-modal">
        <span class="copy-tip copy-fail">{{ $t("copy_fail") }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import Clipboard from 'clipboard';
import i18n from 'i18next';
import axios from '@/api/axios';
import errHandler from '@/utils/error-handler';

import delay from '_/delay';
import { getContextUserId } from '@/loaders/context';

const apiRetryRenderTask = (albumId) => {
  return axios({
    method: 'get',
    url: '/show/renderTask/retry',
    params: {
      albumId,
    },
  });
};

export default {
  name: 'RenderPhotoItem',
  props: {
    picture: {
      type: Object,
      required: true,
    },
    from: {
      type: String,
    },
  },
  data() {
    return {
      previewLink: '',
      clipboard: null,
      timer: null,
    };
  },
  mounted() {
    // this.clipboard = this.clipboard || new Clipboard('.el-icon-copy-document');
    if (this.picture.imgType === 1 && this.picture.loadPath) {
      this.clipboard = this.clipboard || new Clipboard(`[data-clipboard-id='${this.picture.pictureCatalogId}']`);
      this.clipboard.on('success', async () => {
        this.$el.querySelector('.copy-tip-modal').classList.add('show-copy-tip-modal');
        await delay(1000);
        this.$el.querySelector('.copy-tip-modal').classList.remove('show-copy-tip-modal');
      });
      this.clipboard.on('error', async () => {
        this.$el.querySelector('.copy-tip-modal').classList.add('show-copy-tip-modal');
        await delay(1000);
        this.$el.querySelector('.copy-tip-modal').classList.remove('show-copy-tip-modal');
      });
      const { protocol, host } = window.location;

      this.previewLink = `${protocol}//${host}/render-photo-preview?p=${btoa(JSON.stringify({
        renderRelateId: this.picture.renderRelateId,
        albumId: this.picture.renderAlbumId,
        groupAppId: this.from,
      }))}`;
    }
  },
  computed: {
    /** @return { boolean } */
    isCreator() {
      return this.picture.userId === getContextUserId();
    },
    /** @return { string } */
    resolution() {
      if (this.picture.imgType === 1) return '360°';
      const width = parseInt(this.picture.resolution, 10);
      if (width === 1024) return i18n.t('model.photoSize.preview');
      if (width === 1920) return i18n.t('model.photoSize.standard');
      if (width === 4000) return i18n.t('model.photoSize.hd');
      return i18n.t('model.photoSize.unknown');
    },
  },
  methods: {
    async renderRetry() {
      try {
        await apiRetryRenderTask(this.picture.renderAlbumId);
        this.$closest('RenderPhoto').getPhotos();
        // this.picture.status = 0;
      } catch (e) {
        errHandler(e);
      }
    },
  },
  destroyed() {
    if (this.clipboard) {
      this.clipboard.destroy();
    }
  },
  // filters: {
  //   resolution(value) {
  //     if (imgType === 1) return '360°';
  //     const width = parseInt(value, 10);
  //     if (width === 1024) return 'Preview';
  //     if (width === 1920) return 'Standard';
  //     if (width === 4000) return 'HD';
  //     return 'unknown';
  //   },
  // },
};
</script>

